import React from 'react';
import Image from 'react-bootstrap/lib/Image';
export default class ProfileImage extends React.Component {
    render() {
        return (
            <Image src="https://images-na.ssl-images-amazon.com/images/G/01/img15/pet-products/small-tiles/23695_pets_vertical_store_dogs_small_tile_8._CB312176604_.jpg" responsive thumbnail />
        );
    }
}



